@import url("https://fonts.googleapis.com/css2?family=Grandstander:wght@300&display=swap");

:root{
  --sidebar-flex:1.5;
  --condiv-flex:6;
  --fontsize-p:1.2rem;
  --fontsize-nav:1.6rem;
  
  }

  body{
    font-family: 'Times New Roman', Times, serif;
  }
  
   nav{
  
      background:linear-gradient(60deg,#d1ea40 20%, #10ad2a 80%);
      text-align: center;
      }
      nav ul{
  
        box-sizing: border-box;
        display:inline-block;
        line-height:45px;
      }
      nav ul li{
        padding-right: 80px;
        display:inline-block;
        text-align: center;
        width:30%;
      }
      .Link{
        
        color:bisque;
        display:inline-block;
        width:30%;
        font-size: 100%;
        text-align: left;
  
  
      }

      .Link:hover{
        color: rgb(209, 255, 2);
      }
       .profilepic{
       width:150px;
        height:150px;
        border-radius: 50%;
      }

      .condiv{
  
        background-color: #07f1de;
        text-align: center;
        min-height: 75vh;
        padding-top: 5%;
  
  
      }
      p{
        overflow:auto;
      }
      .social a{
        color: rgb(13, 227, 235);
        padding-left: 2%;
      }
      .social a:hover{
        color: gray;
      }
      
      .active
      {
      font-weight: bolder;
      }
  
  
  
  @media only screen and (min-width:769px){
    body{
      font-family: 'Times New Roman', Times, serif;
      color: #2a4458
    }

  .App
  {
  display: flex;
  width: 100vw;
  min-height: 100vh;
  }
  .condiv
  {
  padding: 100px ;
  background-color: #0efcdc;
  flex:var(--condiv-flex);
  text-align: center;
  }
  nav
  {
  text-align: center;
 
  padding:60px 30px 0px 30px ;
  flex:var(--sidebar-flex);
  
  background:linear-gradient(60deg,#d1ea40 20%, #10ad2a 80%);
  min-height:100vh;
  }
  nav ul
  {
    font-size:var(--fontsize-nav) ;
    text-align: center;
    list-style-type: none;
    text-decoration: none;
    color: white !important;
  }

  
  nav ul li
  {
    padding-right: 70px;
    margin:30px;
    text-align: center;
  }

  .Link{
    color:bisque;
    font-size: 100%;
    line-height: 45px;
    padding-bottom: 20%;
  }

  .Link:hover{
    color: rgb(225, 250, 2);
  }



  .subtopic
  {
  margin:10px 10px 30px 10px !important;
  }

  p
  {
  font-size: var(--fontsize-p);
  font-size: 1.3rem;
  
  padding:2%;
  }

  h1,h3,p
  {
  margin: 10px;
  }


 
  .profilepic{
    width:300px;
    height:300px;
    border-radius: 50%;
    padding:5%;
  }
  .social a{
    color: rgb(9, 250, 250);
    padding:1%;
    width:50%;
    height:100%;
  }

  .social a:hover{
    color: gray;
  }

  }